import React from 'react'
import Message from './message/Message'
import MessageSender from './message-sender/MessageSender'
import s2 from '../../s1-main/App.module.css'
import FriendMessage from './friend-message/FriendMessage'
import avatar from './avatar.png'
// import { relative } from 'path'

/*
* 1 -   MessageType
* 2 -   MessagePropsType   Message.tsx
* 3 -   Message.tsx   
* 4 -   2, 3   FriendMessage.tsx
* 5 -      
* */

//      any
export type MessageType = {
    id:number;
    user: {
        avatar: string;
        name: string;
    }
    message: {
        text: string;
        time: string;
    }
}

//    
export const message0: MessageType = {
    id: 0,
    user: {
        avatar: avatar, //  
        name: 'Some Name',  //  
    },
    message: {
        text: 'some textsome textsome textsome textsome textsome textsome text', //  
        time: '22:00', //  
    },
}
export const friendMessage0: MessageType = {
    id: 100,
    user: {
        avatar: avatar, //  
        name: 'Friend Name', //  
    },
    message: {
        text: '    css', //  
        time: '22:00', //  
    },
}

const HW1 = () => {

    const divStyle = {
        display: "grid",
        gridTemplateColumns: "1 2",
        gridTemplateRow: "1 2"
    }

    return (
        <div id={'hw1'} style={{paddingTop:"60px"}}>
            <div className={s2.hwTitle}>Hometask  1</div>
            <div className={s2.hw}>
                {/*  ( )*/}
                <div style={divStyle}>
                    <Message message={message0} />
                    <FriendMessage message={friendMessage0} />
                </div>

                {/*    ( )*/}
                <MessageSender M={Message} />
            </div>
        </div>
    )
}

export default HW1
